@mixin btn($color) {
    display: inline-block;
    background-color: white;
    color: $color;
    border-radius: 15px;
    padding: 10px;
    font-size: 12px;
    border:2px solid $color;
    &:hover{
        color: white;
        background-color: $color;
        cursor: pointer;
    }
}
*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    text-align: center;
    word-break: break-word;
    transition: all 0.1s;

}

header{
    position: fixed;
    top: 0;
    width: 100%;
    z-index: 2;
    display: flex;
    justify-content: space-between;
    padding: 5px 4% ;
    background: linear-gradient(royalblue, skyblue);
    box-shadow: 0 0 5px 1px black;
    button{
        display: inline-block;
        background-color: skyblue;
        padding: 10px 20px;
        justify-self: center;
        align-self: center;
        border-radius: 15px;
        font-weight: 900;
        &:hover{
            background-color: rgb(80, 187, 230);
            cursor: pointer;
        }
    }
    .check{
        h2{
            font-size: 15px;
            box-shadow: 0 0 1px 2px rgb(197, 197, 197);
        }
        h3{
            background-color: royalblue;
            color: white;
            margin: 2px auto;
            border-radius: 15px;
            font-size: 15px;
        }
    }
    h2{
        background-color: orange;
        border-radius: 15px;
        padding: 5px 10px;
        &:hover{
            transform: scale(1.1);
            cursor: pointer;
        }
    }
}
img{
    width: 100%;
}
main{
    padding-top: 80px;
    width: 80%;
    margin: 10px auto;
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;

  
    
    .cards{
        flex-basis: 27%;
        padding: 20px;
        margin: 20px;
        border-radius: 15px;
        box-shadow: 0 0 15px 2px rgb(63, 63, 63);
        display: flex;
        flex-direction: column;
        justify-content: space-between;
      
        &:hover{
            border: 3px solid teal;
            cursor: pointer;
        }
        p{
            background-color: greenyellow;
        }
        .button{
            margin: 10px auto;
            button{
               @include btn(rgb(141, 142, 223));
               margin:5px;
            }
        }
    }
}
.details{
    padding-top: 80px;
   width: 50%;
   margin: 10px auto;
   display: flex;
   flex-direction: column;
   img{
       width: 250px;
       margin: 2rem;
   }
   div{
       display: flex;
       justify-content: space-around;
       div{
           display: flex;
           flex-direction: column;
           justify-content: center;
           button{
            @include btn(rgb(141, 142, 223))
           }
       }
   }
}
.loading{
    margin: 10px auto;
    border: 3px solid grey;
    // box-shadow: 0 0 3px 2px gray;
    padding: 50px;
    border-bottom: none;
    border-radius: 50%;
    animation: roll 0.3s linear infinite;
}

@keyframes roll {
  from{
      transform: rotate(0deg);
  }  
  to{
      transform: rotate(360deg);
  }
}
.fixed{
    background: red;
}

.checkout{
    padding-top: 80px;
    width: 50%;
    margin: 10px auto;
    font-size: 15px;
    button{
        @include btn(rgb(52, 183, 243)) 
    }
    div{
        display: flex;
        align-items: center;
        justify-content: center;
        box-shadow: 0 0 5px 2px dimgray;
        border-radius: 15px;
        margin: 10px auto;
        padding: 15px;
        *{
            margin: 0 10px;
        }
        img{
            width: 50px;
        }
        h4{
            background-color: red;
            border-radius: 50%;
            padding: 5px 10px;
            color: white;
            &:hover{
                cursor: pointer;
                transform: scale(1.1);
            }
         
        }
        
      
        button{
            @include btn(rgb(247, 93, 73)) 
        }
    }
}
.form{
    margin: 10px;
    padding-top: 80px;
    display: flex;
    justify-content: space-between;
.sign-in{
    box-shadow: 0 0 5px 2px dimgray;
    padding: 25px;
    border-radius: 15px;
    width: 25vw;
    display: flex;
    flex-direction: column;
    justify-content: center;
    input{
        width: 100%;
        font-size: 25px
    }
    input[type=submit]{
        @include btn(rgb(247, 93, 73)) ;
        display: block;
        width: 100%;
    
    } 

    }
    .sign-out{
        box-shadow: 0 0 5px 2px dimgray;
        padding: 25px;
        border-radius: 15px;
        width: 25vw;
        display: flex;
        flex-direction: column;
        justify-content: center;
        input{
            width: 100%;
            font-size: 25px
        }
        input[type=submit]{
            @include btn(rgb(140, 73, 247)) ;
            display: block;
            width: 100%;
        
        } 
    
        }
    *{
        margin: 10px auto;
    }
    .google{
        @include btn(rgb(133, 170, 124)) ;
        width: 100%;
    }
}
